<template>
  <div style="margin: 0px 15px;">
    <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选</el-checkbox>
    <div style="margin: 15px 0;"></div>
    <el-checkbox-group v-model="checkedGoods" @change="handleCheckedGoodsChange" class="vertical-checkbox-group">
      <el-checkbox v-for="good in localGoods" :label="good.id" :key="`${good.id}-${good.name}`" style="height: inherit;" border>
        <el-card :body-style="{ padding: '0px' }" shadow="hover">
          <img :src="good.image" class="image"> <!-- 动态绑定商品图片 -->
          <div style="padding: 14px;">
            <span>{{ good.name }} - ¥{{ good.price }}</span> <!-- 动态绑定商品名称和价格 -->
            <div class="bottom clearfix">
              <el-button type="danger" class="button" style="margin-left:200px" @click="delete_good(good.id)">删除</el-button>
            </div>
          </div>
        </el-card>
      </el-checkbox>
    </el-checkbox-group>
    <!-- 新增：付款按钮 -->
    <div style="text-align: center; margin-top: 20px;">
      <el-button type="primary" @click="payFromCart">付款</el-button>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    goods: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      checkAll: false,
      checkedGoods: [],
      isIndeterminate: true,
      localGoods: [], // 用于存储 goods 的副本
    };
  },
  watch: {
    goods: {
      immediate: true,
      handler(newVal) {
        this.localGoods = [...newVal]; // 监听 goods 变化并更新副本
      },
    },
  },
  methods: {
    delete_good(id) {
      this.localGoods = this.localGoods.filter(good => good.id !== id); // 修改副本而非 props
      this.$emit('update:goods', this.localGoods); // 通知父组件更新 goods
    },
    handleCheckAllChange(val) {
      this.checkedGoods = val ? this.localGoods.map(good => good.id) : [];
      this.isIndeterminate = false;
    },
    handleCheckedGoodsChange() {
      // 删除未使用的变量 value
      const checkedCount = this.checkedGoods.length; // 直接使用 this.checkedGoods.length
      this.checkAll = checkedCount === this.localGoods.length;
      this.isIndeterminate = checkedCount > 0 && checkedCount < this.localGoods.length;
    },
    // 新增：付款逻辑
    payFromCart() {
      const selectedGoods = this.localGoods.filter(good => this.checkedGoods.includes(good.id));
      const totalAmount = selectedGoods.reduce((sum, good) => sum + good.price, 0);

      if (totalAmount === 0) {
        this.$message.warning('请选择要购买的商品');
        return;
      }

      this.$prompt('请输入信用卡号', '支付', {
        confirmButtonText: '确认支付',
        cancelButtonText: '取消',
        inputPattern: /^\d{16}$/, // 验证16位数字
        inputErrorMessage: '请输入有效的16位信用卡号',
      }).then(() => {
        // 模拟调用银行支付系统
        this.$message.success(`支付成功，已扣除余额 ¥${totalAmount.toFixed(2)}`);
        // 清空购物车中的已选商品
        this.localGoods = this.localGoods.filter(good => !this.checkedGoods.includes(good.id));
        this.checkedGoods = []; // 清空选中商品列表
        this.$emit('update:goods', this.localGoods); // 通知父组件更新 goods

        // 通知管理员更新销售数据
        this.$emit('payment-complete', selectedGoods, totalAmount);
      }).catch(() => {
        this.$message.info('已取消支付');
      });
    },
  },
};
</script>

<style scoped>
.image {
  width: 100%;
  display: block;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}

.clearfix:after {
  clear: both
}

.button {
  padding: 0;
  float: right;
}

.vertical-checkbox-group {
  display: flex;
  flex-direction: column;
}

</style>