  methods: {
    handleClose(done) {
      this.$confirm('确认关闭？')
        .then(() => {
          done();
        })
        .catch(() => {});
    },
    classifyClick(tab) {
      console.log('当前选中的分类:', tab.label);
    },
    filterByCategory(category) {
      if (category === '全部') {
        this.goods = [...this.originalGoods];
      } else {
        this.goods = this.originalGoods.filter(good => good.category === category);
      }
    },
    add_to_cart(good) {
      if (good) {
        // 检查购物车中是否已存在该商品
        const exists = this.cart.some(item => item.id === good.id);
        if (!exists) {
          this.cart.push(good);
          this.$message({
            showClose: true,
            message: `${good.name} 已加入购物车`,
            duration: 1000,
          });
        } else {
          this.$message({
            showClose: true,
            message: `${good.name} 已存在于购物车中`,
            duration: 1000,
          });
        }
      }
    },
    updateCart(newCart) {
      this.cart = newCart;
    },
    searchGoods() {
      if (this.searchvalue.trim() === '') {
        this.$message.warning('请输入搜索内容');
        return;
      }
      // 使用 filter 方法对商品进行筛选
      const filteredGoods = this.originalGoods.filter(good => 
        good.name.toLowerCase().includes(this.searchvalue.toLowerCase())
      );
      if (filteredGoods.length === 0) {
        this.$message.info('未找到相关商品');
        this.goods = []; // 清空商品列表
      } else {
        this.goods = filteredGoods; // 更新 goods 为搜索结果
      }
    },
    buyNow(good) {
      if (!good) {
        this.$message.warning('商品信息无效');
        return;
      }

      const currentBalance = parseFloat(this.balanceState.money.replace("￥", ""));
      if (currentBalance < good.price) {
        this.$message.error('余额不足，请先充值');
        return;
      }

      // 扣除余额
      const newBalance = currentBalance - good.price;
      this.balanceState.money = `￥${newBalance.toFixed(2)}`;

      this.$message.success(`购买成功，已扣除余额 ¥${good.price.toFixed(2)}`);
    },
    rate_change(score) {
      this.rated = score;
      this.$message({
        showClose: true,
        message: "评分更改成功",
        duration: 1000,
      });
    },
    submitComment() {
      if (!this.hot_value.passage.trim()) {
        this.$message.warning('评论内容不能为空');
        return;
      }
      this.comments.push({ ...this.hot_value });
      this.hot_value.passage = ''; // 清空输入框
      this.$message.success('评论发表成功');
    },
  },
  mounted() {
    // 初始化 goods 为原始商品数据
    this.goods = [...this.originalGoods];
    this.timerId = setTimeout(() => {
      this.loading = false;
    }, 1500);
  },
};
</script>

